    z-index: 2;
    background-color: #212327;

    .spinner-border {
      position: absolute;
      left: 50%;
      top: 50%;
    }
  }

  .btn {
    display: flex;
    justify-content: space-between;

    .interval {    
      margin: 0 52px 10px;
    }

    .candle-data-area {
      .symbol-value {
        color : #aaaaaa;
        margin-right: 10px;
      }
      .candle-data {
        font-size: 10px;
        span {
          margin-right: 4px;
        }
        .rise {
          color : #4bffb5
        }
        .down  {
          color : #ff4976;
        }
      }
    }
  }

  .btn.one .one,
  .btn.five .five,
  .btn.five .five,
  .btn.fifteen .fifteen,
  .btn.thirty .thirty,
  .btn.sixty .sixty,
